.main {
  border-top: 0;
  padding: 10px;
  width: 60px; }
  .main.expandedBox {
    width: 100%; }
  .main a {
    white-space: nowrap; }

.brandList {
  overflow-x: hidden;
  max-height: 500px; }

.listItem {
  white-space: normal; }

.searchBox {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: flex-start; }
  .searchBox .inputBox {
    flex: 0;
    display: none; }
  .searchBox .buttonBox {
    flex-grow: 0;
    flex-basis: 12px; }
    .searchBox .buttonBox button {
      margin-left: 0;
      display: block; }
      .searchBox .buttonBox button span {
        -webkit-transform: scale(1, 1);
        -moz-transform: scale(1, 1);
        -ms-transform: scale(1, 1);
        -o-transform: scale(1, 1);
        transform: scale(1, 1); }

.expandedBox .inputBox {
  flex: 1;
  display: block; }

.expandedBox .buttonBox button {
  margin-left: 8px;
  display: block; }
  .expandedBox .buttonBox button span {
    -webkit-transform: scale(-1, 1);
    -moz-transform: scale(-1, 1);
    -ms-transform: scale(-1, 1);
    -o-transform: scale(-1, 1);
    transform: scale(-1, 1); }
