.loaderApp {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: flex-start; }
  .loaderApp > div {
    -webkit-transition: all, 0.3s, ease;
    -moz-transition: all, 0.3s, ease;
    -o-transition: all, 0.3s, ease;
    transition: all 0.3s ease; }
  .loaderApp .brandList {
    -webkit-flex: 0 1 12px;
    flex: 0 1 12px;
    margin-right: 10px; }
  .loaderApp .brandListActive {
    -webkit-flex: 1;
    flex: 1; }
  .loaderApp .advertList {
    -webkit-flex: 3;
    flex: 3; }
  .loaderApp .advertListActive {
    -webkit-flex: 1;
    flex: 1; }

@media (max-width: 750px) {
  .loaderApp {
    -webkit-flex-direction: column;
    flex-direction: column; }
    .loaderApp .brandList {
      -webkit-flex: 0;
      flex: 0;
      margin-right: 0;
      width: 100%;
      overflow-y: auto; }
    .loaderApp .advertList {
      -webkit-flex: 1;
      flex: 1; } }
