.fa2 {
  font-family: FontAwesome;
  font-style: normal;
  font-weight: 400;
  -webkit-font-smoothing: antialiased; }

.buttonLeftMargin {
  margin-left: 5px; }

.propertyEditor {
  margin-top: 5px;
  margin-bottom: 5px; }

.list {
  overflow-y: auto;
  max-height: 400px; }

.valuesListWrapper {
  position: relative; }

.valuesListWrapperLoader {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: grey;
  background-image: url("/img/loading.gif");
  background-position: center;
  background-repeat: no-repeat;
  opacity: 0.5;
  top: 0;
  z-index: 100; }

.notAllowed {
  cursor: not-allowed; }
